  export default {
    name: 'integralOrder',
    data (){
      return {
        list: []
      }
    },
    methods: {
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      getListData(){
        this.http.get(this.ports.integral.orderlist, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.list = data.res
          }else{
//            this.showToastTxtOnly(res.msg)
          }
        })
      }
    },
    mounted (){
      this.getListData()
    }
  }



